body {
  background: #fff;
  font-family: Roboto, sans-serif;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

p, h1, h2, h3, h4, h5, h6 {
  margin: 0;
}

ul, ol {
  margin: 0;
  padding: 0;
}

li {
  margin: 0;
  padding: 0;
  list-style: none;
}

button {
  cursor: pointer;
}

.main-container {
  width: 100%;
  margin: 0 auto;
  padding-left: 20px;
  padding-right: 20px;
}

@media screen and (min-width: 320px) {
  .main-container {
    max-width: 320px;
  }
}

@media screen and (min-width: 768px) {
  .main-container {
    max-width: 768px;
    padding-left: 32px;
