.tui-pagination {
  height: 40px;
  width: auto;
  justify-content: center;
  align-items: center;
  margin-left: auto;
  margin-right: auto;
  padding-bottom: 60px;
  font-size: 12px;
  line-height: 1.33333;
  display: flex;
}

.tui-page-btn {
  width: 40px;
  height: 40px;
  color: #000;
  cursor: pointer;
  background: #fff;
  border: none;
  border-radius: 5px;
  outline: none;
  justify-content: center;
  align-items: center;
  margin: 0;
  font-weight: 500;
  text-decoration: none;
  display: flex;
}

.tui-page-btn:hover {
  color: #fff;
  background-color: #ff6b08;
}

.tui-pagination .tui-ico-first, .tui-pagination .tui-ico-prev, .tui-pagination .tui-ico-next, .tui-pagination .tui-ico-last, .tui-pagination .tui-ico-ellip {
  vertical-align: middle;
  background: url("data:image/png;base64,") no-repeat;
  font-size: 0;
  display: inline-block;
}

.tui-pagination .tui-ico-first {
  width: 11px;
  height: 10px;
  background-position: 0 0;
}

.tui-pagination .tui-ico-prev {
  width: 6px;
  height: 10px;
  background-position: -26px 0;
}

.tui-pagination .tui-ico-next {
  width: 6px;
  height: 10px;
  background-position: -13px -24px;
}

.tui-pagination .tui-ico-last {
  width: 11px;
  height: 10px;
  background-position: 0 -12px;
}

.tui-pagination .tui-ico-ellip {
  width: 10px;
  height: 10px;
  vertical-align: top;
  background-position: 0 -24px;
  margin-top: 16px;
}

.tui-pagination .tui-is-disabled .tui-ico-prev {
  background-position: -26px -12px;
}

.tui-pagination .tui-is-disabled .tui-ico-first {
  background-position: -13px 0;
}

.tui-pagination .tui-is-disabled .tui-ico-next {
  background-position: -21px -24px;
}

.tui-pagination .tui-is-disabled .tui-ico-last {
  background-position: -13px -12px;
}

.tui-prev-is-ellip, .tui-next-is-ellip {
  padding-bottom: 5px;
}

.tui-prev, .tui-next, .tui-first, .tui-last {
  width: 40px;
  height: 100%;
  color: #000;
  background-color: #f7f7f7;
  border: none;
}

.tui-pagination .tui-is-selected, .tui-pagination strong {
  color: #fff;
  background-color: #ff6b08;
  margin-left: 8px;
  margin-right: 8px;
}

